<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">检查数据提取</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">提取数据记录</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
          <div class="button-content">
            <!-- <Button type="primary">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button> -->
          </div>
        </div>
        <div class="contentCellBody1">
          <Form
            :model="formValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="申请时间：">
                  <DatePicker split-panels type="daterange" placeholder="请选择时间" :value="formValidate.applyTime" @on-change="formValidate.applyTime=$event"></DatePicker>
              </FormItem>
              <FormItem label="提取机构：" prop="emergeLevel">
                  <treeselect v-model="formValidate.extraUnit"  valueConsistsOf="ALL" 
              :limit="3" :multiple="true" :options="data1" placeholder='请选择' style="width:300px"/>
              </FormItem>
              <FormItem label="审核状态：">
                <CheckboxGroup v-model="formValidate.workFlowState">
                  <Checkbox
                    v-for="item in workFlowStates"
                    :label="item.code"
                    :key="item.code"
                  >{{item.name}}</Checkbox>
                </CheckboxGroup>
              </FormItem>

            </Row>
            <Row>
              <FormItem label="紧急程度：">
                  <CheckboxGroup v-model="formValidate.emergeLevel">
                    <Checkbox
                      v-for="item in emergeLevels"
                      :label="item.code"
                      :key="item.code"
                    >{{item.name}}</Checkbox>
                  </CheckboxGroup>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('1')" style="width:89px">查询</Button>
                <Button type="primary"  @click="handleSubmit('2')" style="margin-left:10px">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="item in tableHeader" :key="item.name">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody" :key="item.id">
                  <td>
                   {{formValidate.pageSize*(formValidate.pageNo-1)+index+1}}
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.extraUnit"
                    >
                      <span
                        slot="reference"
                        class="textOverflow textMaxWidth"
                        >{{item.extraUnit}}</span>
                    </el-popover>
                  </td>
                  <td>{{item.extraTime}}~{{item.extraTime2}}</td>
                  <td>{{item.deadLine?item.deadLine.substr(0,10):''}}</td>
                  <td>{{item.permissionTime?item.permissionTime.substr(0,10):''}}~{{item.permissionTime2?item.permissionTime2.substr(0,10):''}}</td>
                  <td>{{item.emergeLevel}}</td>
                  <td>
                    <span class="operationSpan view"  @click="watchFiles(item.batchId)">查看</span>
                  </td>
                  <td>{{item.state}}</td>
                  <td>{{item.applyTime.substr(0,19)}}</td>
                  <td>{{item.extraApplicant}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.extraApplicantUnit"
                    >
                      <span
                        slot="reference"
                        class="textOverflow textMaxWidth"
                        >{{item.extraApplicantUnit}}</span>
                    </el-popover>
                  </td>
                  <td>{{item.endTime?item.endTime.substr(0,19):''}}</td>
                  <td style="width:100px">
                    <span class="operationSpan" @click="watchfun(item)">申请详情</span>
                    <span class="operationSpan" @click="showProcess(item)">流程详情</span>
                  </td>
                  <td style="min-width:100px">
                    <span class="operationSpan" @click="logDetail(item)">日志详情</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </Row>
         <div class="flexCenter pageBox">
              <Page :current.sync="formValidate.pageNo" :total="rowCount" :page-size="formValidate.pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
          </div>
        </div>
        <Modal :mask-closable="false"   v-model="modal1" title="文件列表">
             <div class="fujian">
              <div class="fujian-title">附件:</div>
              <div v-for="(item,index) in fileList" :key="index">
                <span
                  style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
                  class="cursorPointer"
                  @click="fileDownload(item.fileId, item.filename)"
                >{{item.filename}}</span>
              </div>
            </div>
            <div slot="footer">
            </div>
        </Modal>
        <Modal :mask-closable="false"   v-model="showModal" title="流程详情" class="dialog"  width="80%">
          <processMessage
            :data-obj="dataObj"
            v-if="showModal"
          ></processMessage>
          <div slot="footer"></div>
      </Modal>
      </div>
    </div>
  </div>
</template>
<script>
import Treeselect from '@riophae/vue-treeselect'
import processMessage from "@/components/moneyRiskAssess/common/processMessage"; //过程信息
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
  components: {Treeselect, processMessage},
  created() {
    let that = this;
    let data = JSON.parse(JSON.stringify(this.formValidate));
    data.applyTime = JSON.stringify(data.applyTime);
    data.extraUnit = JSON.stringify(data.extraUnit);
    data.workFlowState = JSON.stringify(data.workFlowState);
    data.emergeLevel = JSON.stringify(data.emergeLevel);
    data = JSON.stringify(data);
    this.axios
      .post("/extract/showRecordInfo", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code=="200"){
              that.rowCount = response.data.total;
              that.tableBody = response.data.records;
        }
      })
      .catch(error => {});
      this.axios
          .get("/extract/getExtractDic/EMERCRGENCY_LEVEL")
          .then(function(response) {
                if (response.code=="200"){
                    that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
                }
          })
          .catch(function(error) {});
        this.axios
        .post("/system/unit/getExtractUnit")
        .then(response => {
          if (response.code == "200") {
            that.data1 = response.data;
            that.selectTreeCleanUp(that.data1);
          }
        })
        .catch(error => {});
  },
  data() {
    return {
      showModal: false,
      rowCount: 0,
      modal1: false,
      dataObj:{
        businessId: "",
        name: ""
      },
      fileList: [],
      tableHeader: [
        { name: "序" },
        { name: "提取机构" },
        { name: "提取数据期间" },
        { name: "提交截止日期" },
        { name: "权限使用时间" },
        { name: "紧急程度" },
        { name: "附件" },
        { name: "审核状态"},
        { name: "申请时间" },
        { name: "申请人" },
        { name: "申请人所属部门" },
        { name: "审核完成时间" },
        { name: "操作" },
        { name: "下载记录" }
      ],
      tableBody: [
      ],
      emergeLevels: [
      ],
      workFlowStates:[
        { code: "0", name: "审核通过"},
        { code: "1", name: "审核退回"},
        { code: "2", name: "审核中"}
      ],
      data1: [ 
      ],
      formValidate: {
        applyTime: ["",""], //申请时间
        extraUnit: [], //提取机构
        workFlowState: [], //审核状态
        emergeLevel: [], //紧急程度
        flag: "1",
        pageSize: 10,
        pageNo: 1,
      }
    };
  },
  methods: {
    showProcess(item) {
      this.dataObj = {
        businessId: item.batchId,
        name: "检查数据提取"
      };
      this.showModal = true;
  },
    handleSubmit(flag) {
      let that = this;
      this.formValidate.flag = flag;
      this.formValidate.pageNo = 1;
      let data = JSON.parse(JSON.stringify(this.formValidate));
      data.applyTime = JSON.stringify(data.applyTime);
      data.extraUnit = JSON.stringify(data.extraUnit);
      data.workFlowState = JSON.stringify(data.workFlowState);
      data.emergeLevel = JSON.stringify(data.emergeLevel);
      data = JSON.stringify(data);
      this.axios
        .post("/extract/showRecordInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code=="200"){
                that.rowCount = response.data.total;
                that.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
    selectTreeCleanUp(treeDatas) {
        for (let i = 0; i < treeDatas.length; i++) {
          if (treeDatas[i].children && treeDatas[i].children.length >0) {
              this.selectTreeCleanUp(treeDatas[i].children);
          }
          else this.$delete(treeDatas[i], "children");
        }
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        "token": "",
        "fileId": filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" },
        })
        .then(response => {
            let data2 = JSON.stringify({
              "url": response.message,
              "fileName": fileName
            });
             this.axios
              .post(`/system/remote/getFileInfo`,data2,
              {
                  headers: { "Content-Type": "application/json" },
                  responseType: "blob"
              }
              )
              .then(res =>{
                  this.download(res, fileName);
              })
              .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    watchFiles(id){
        this.axios
        .post(`/system/remote/getFileList/${id}`)
        .then(res =>{
              if (res.code == "200"){
                this.fileList = res.data;
                this.modal1 = true;
              }
        })
        .catch(function(error) {});
    },
    pageSizefun(a){
      this.formValidate.pageSize = a;
      if (this.formValidate.pageNo == 1){
          this.pageChange(1);
      }
    },
    pageChange(pageNo) {
      let that = this;
      this.formValidate.pageNo=pageNo;
      let data = JSON.parse(JSON.stringify(this.formValidate));
      data.applyTime = JSON.stringify(data.applyTime);
      data.extraUnit = JSON.stringify(data.extraUnit);
      data.workFlowState = JSON.stringify(data.workFlowState);
      data.emergeLevel = JSON.stringify(data.emergeLevel);
      data = JSON.stringify(data);
      this.axios
        .post("/extract/showRecordInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code=="200"){
                that.rowCount = response.data.total;
                that.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
    logDetail(item){
        this.$router.push({name: "optionLog",query: {menuCode: "112006000000", optUser: item.extraApplicant, optDesc: "63号文数据文件"}});
    },
    watchfun(obj){
        this.$router.push({name: "dataExtractionApply2",query: {menuCode: "107003000100", batchId: obj.batchId, Id: "12", disabledFlag: obj.taskStatus == "1"?true:false}});
    }
  }
};
</script>
<style scoped>
.tableBox tbody>tr td:nth-child(3){
  min-width: 200px;
}
.tableBox tbody>tr td:nth-child(5){
  min-width: 200px;
}
.tableBox tbody>tr td:nth-child(7){
  min-width: 100px;
}
.tableBox tbody>tr td:nth-child(9),.tableBox tbody>tr td:nth-child(13){
  min-width: 200px;
}
.rightTopbtn {
  margin: 15px 4px;
  float: right;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
</style>



